<template>
	<div>
		<div class="back-title"><span>结算中心</span></div>
		<div class="pagebox" style="margin: 10px 0;">
			 <Alert type="warning" show-icon>您还未绑定提款账号，请尽快绑定银行卡，以免影响提现， <router-link to="/income/addaccountsback">去绑定</router-link></Alert>
			 <Row class-name="panlbox">
		        <Col span="8"><h3>45.45</h3>可提现金额（元）
		        	<p style="margin-top: 5px;">
		        		<Tooltip placement="bottom" content="提现最低金额为100元" disabled="disabled">
		        			<Button type="primary" disabled>去提现</Button>
		        		</Tooltip>
		        	</p>
		        </Col>
		        <Col span="8"><h3>45.45</h3>已提现金额（元）</Col>
		        <Col span="8"><h3>45.45</h3>总收益（元）</Col>
		    </Row>
		    <div class="tips">
		    	<p>提现要求：成功绑定银行卡，且可手动提现金额 大于100元</p>
				<p>提现时间：每月2日-4日（当月开通分成收益需次月提现）</p>
				<p>提现到账：个人用户到账金额为平台代缴个税后金额，媒体和企业用户请查看 <a href="">结算说明</a></p>
		    </div>
		    <div class="item-h3">提现记录</div>
		    <Table border :columns="columns1" :data="incomelist" no-data-text="暂无提现记录"></Table>
		</div>
	</div>
</template>

<script>
 export default {
   name: 'notice',
   data () {
     return {
       columns1: [
         {
           title: '提现日期',
           key: 'date',
           className: 'incomelist'
         },
         {
           title: '提现金额',
           key: 'total_income',
           width: '200',
           className: 'incomelist'
         },
         {
           title: '状态',
           key: 'income_status',
           className: 'incomelist',
           width: '100',
           render: (h, params) => {
             const color = params.row.income_status === 1 ? 'blue' : params.row.income_status === 0 ? 'yellow' : params.row.income_status === 2 ? 'green' : 'yellow'
             const text = params.row.income_status === 1 ? '打款中' : params.row.income_status === 0 ? '处理中' : params.row.income_status === 2 ? '已打款' : '处理中'
             return h('Tag', {
               props: {
                 color: color
               }
             }, text)
           }
         }
       ],
       incomelist: [
         {
           total_income: '31.22',
           income_appreciate: '12.11',
           income_status: 2,
           date: '2016-10-03'
         },
         {
           total_income: '31.22',
           income_appreciate: '11.12',
           income_status: 1,
           date: '2016-10-04'
         },
         {
           total_income: '31.22',
           income_appreciate: '11.12',
           income_status: 0,
           date: '2016-10-04'
         },
         {
           total_income: '31.22',
           income_appreciate: '11.12',
           income_status: 4,
           date: '2016-10-04'
         }
       ]}
   }
 }
</script>

<style>
</style>


